.maps-container {
    display: flex;
    flex-direction: row;
}
.maps-search-container-div {
    width: 25%;
    height: inherit;
    display: flex;
    flex-direction: column;
}

.maps-search-div {
    margin: 5px 5px 0px 10px;
}
.search-input {
    height: 32px;
    width: 70%;
    z-index: -1;
    margin: 2px;
}
.search-results-div {
    margin: 5px 5px 0px 10px;
    overflow: auto;
    height: 500px;
    background-color: lightsteelblue;
    font-weight: bold;
}
#results-table {
   width: 100%;
   border: 0.5px solid grey;
}
#results-table th {
    text-align: left;
}
#results-table tr.selected-row {
    background-color: white;
    width: 100%;
}
.maps-div {
    height: auto;
    width: 75%;
}

@media (max-width: 666px) {
    .search-input {
        height: 32px;
        width: 100%;
    }
} 